import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Table,
  Tabs,
  Input,
  Button,
  Select,
  message
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import DZsplitOrderModal from './DZsplitOrderModal'
import {getNowPageId} from '../../global/components/menuData'
import {getUrlDataObj} from 'util'
const TabPane = Tabs.TabPane

@connect(
  state => ({...state.outWarehouseListSell, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
    }
    const {location = {}, match = {}} = this.props
    const {search = ''} = location
    console.log('location is ', location)
    console.log('match is ', match)
    const {tableActiveKey = ''} = getUrlDataObj(search || '')

    this.tableActiveKey = tableActiveKey
    this.columns = [
      {
        title: '商品信息',
        children: [
          {
            title: '序号',
            width: 100,
            dataIndex: 'num',
            key: 'num',
            render: (text, record, index) => index + 1
          }, {
            title: '关联采购订单',
            width: 150,
            dataIndex: 'linkScmOrderCode',
            key: 'linkScmOrderCode'
          }, {
            title: '商品编码',
            width: 100,
            dataIndex: 'productCode',
            key: 'productCode'
          }, {
            title: '商品名称',
            width: 100,
            dataIndex: 'productName',
            key: 'productName'
          }, {
            title: '下单数量',
            width: 100,
            dataIndex: 'planQty',
            key: 'planQty'
          }, {
            title: '销售单位',
            width: 100,
            dataIndex: 'saleUnit',
            key: 'saleUnit'
          }, {
            title: '基础单位',
            width: 150,
            dataIndex: 'unit',
            key: 'unit'
          }, {
            title: '团购价（元）',
            width: 100,
            dataIndex: 'price',
            key: 'price'
          }, {
            title: '发货数',
            width: 100,
            dataIndex: 'shippedQty',
            key: 'shippedQty'
          }, {
            title: '总金额（元）',
            width: 100,
            dataIndex: 'amount',
            key: 'amount'
          }, {
            title: '是否套档',
            width: 100,
            dataIndex: 'kitFlagDesc',
            key: 'kitFlagDesc'
          }, {
            title: '全量发货',
            width: 100,
            dataIndex: 'allSendFlag',
            key: 'allSendFlag'
          }, {
            title: '拣货类型',
            width: 100,
            dataIndex: 'pickingType',
            key: 'pickingType'
          }, {
            title: '规格备注',
            width: 100,
            dataIndex: 'specsRemark',
            key: 'specsRemark'
          }, {
            title: '加工备注',
            width: 200,
            dataIndex: 'handleRemark',
            key: 'handleRemark'
          }, {
            title: '运转模式',
            width: 100,
            dataIndex: 'runningModel',
            key: 'runningModel'
          }
        ]
      }
    ]
    this.columnsType = [
      {
        title: '商品信息',
        children: [
          {
            title: '序号',
            width: 100,
            dataIndex: 'num',
            key: 'num',
            render: (text, record, index) => index + 1
          }, {
            title: '关联采购订单',
            width: 150,
            dataIndex: 'linkScmOrderCode',
            key: 'linkScmOrderCode'
          }, {
            title: '商品编码',
            width: 100,
            dataIndex: 'productCode',
            key: 'productCode'
          }, {
            title: '商品名称',
            width: 100,
            dataIndex: 'productName',
            key: 'productName'
          }, {
            title: '下单数量',
            width: 100,
            dataIndex: 'planQty',
            key: 'planQty'
          }, {
            title: '销售单位',
            width: 100,
            dataIndex: 'saleUnit',
            key: 'saleUnit'
          }, {
            title: '基础单位',
            width: 150,
            dataIndex: 'unit',
            key: 'unit'
          }, {
            title: '团购价（元）',
            width: 100,
            dataIndex: 'price',
            key: 'price'
          }, {
            title: '发货数',
            width: 100,
            dataIndex: 'shippedQty',
            key: 'shippedQty'
          },
          {
            title: '签收数量',
            width: 150,
            dataIndex: 'signQty',
            key: 'signQty',
            render: (text, record) => {
              const {update, modalData} = this.props
              return (
                <Input disabled={modalData.status == 8 || modalData.status == 7} value={record.signQty} onChange={e => {
                  record.signQty = e.target.value
                  update({signQty: record.signQty})
                  this.forceUpdate()
                }} />
              )
            }
          }, {
            title: '差异原因',
            width: 150,
            dataIndex: 'remark',
            key: 'remark',
            render: (text, record) => {
              const {handleShippedorders, ReasonKeyValue, modalData} = this.props
              handleShippedorders()
              return (
                <Select disabled={modalData.status == 8 || modalData.status == 7} defaultValue={record.remark} style={{width: 120}} onChange={(value) => {
                  record.remark = value
                }}>
                  {
                    ReasonKeyValue.map((o, i) => (
                      <Select.Option key={i} value={o}>{o}</Select.Option>
                    ))
                  }
                </Select>
              )
            }
          },
          {
            title: '总金额（元）',
            width: 100,
            dataIndex: 'amount',
            key: 'amount'
          }, {
            title: '是否套档',
            width: 100,
            dataIndex: 'kitFlagDesc',
            key: 'kitFlagDesc'
          }, {
            title: '全量发货',
            width: 100,
            dataIndex: 'allSendFlag',
            key: 'allSendFlag'
          }, {
            title: '拣货类型',
            width: 100,
            dataIndex: 'pickingType',
            key: 'pickingType'
          }, {
            title: '规格备注',
            width: 100,
            dataIndex: 'specsRemark',
            key: 'specsRemark'
          }, {
            title: '加工备注',
            width: 200,
            dataIndex: 'handleRemark',
            key: 'handleRemark'
          }, {
            title: '运转模式',
            width: 100,
            dataIndex: 'runningModel',
            key: 'runningModel'
          }
        ]
      }
    ]
    this.columns1 = [
      {
        title: '订单拆单',
        children: [
          {
            title: '子订单编号',
            width: 150,
            dataIndex: 'splitOrderCode',
            key: 'splitOrderCode'
          }, {
            title: '分组',
            width: 100,
            dataIndex: 'splitGroupName',
            key: 'splitGroupName'
          }, {
            title: '发货系统',
            width: 100,
            dataIndex: 'sendSystem',
            key: 'sendSystem'
          }, {
            title: '商品数量',
            width: 100,
            dataIndex: 'qty',
            key: 'qty'
          }, {
            title: '订单详情',
            width: 150,
            dataIndex: 'ddxq',
            key: 'ddxq',
            render: (text, record, index) => <a onClick={() => {
              const {update, modalData} = this.props
              update({isShowDzSplit: true, dzOrderCode: modalData.orderCode, dzSplitGroupCode: record.splitGroupCode})
            }}>订单详情</a>
          }
        ]
      }
    ]
    this.columns2 = [{
      title: '序号',
      width: 100,
      dataIndex: 'num',
      key: 'num',
      render: (text, record, index) => index + 1
    }, {
      title: '商品名称',
      width: 150,
      dataIndex: 'productName',
      key: 'productName'
    }, {
      title: '单位',
      width: 100,
      dataIndex: 'unit',
      key: 'unit'
    }, {
      title: '单价（元）',
      width: 100,
      dataIndex: 'price',
      key: 'price'
    }, {
      title: '总金额（元）',
      width: 100,
      dataIndex: 'amount',
      key: 'amount'
    }, {
      title: '发货数',
      width: 100,
      dataIndex: 'shippedQty',
      key: 'shippedQty'
    }]
  }
  componentDidMount() {
    this._getAsyncData()
    //权限配置
    const {GetButtonPower} = this.props
    const MEUN = JSON.parse(localStorage.getItem('b2bUserMenu'))
    const ID = getNowPageId(MEUN, '/wms/out-warehouse/outWarehouseList/sell')
    console.log(ID, MEUN, 444)
    GetButtonPower({urlParams: [ID]})
  }
  // 根据tabs的key来判断请求哪个接口
  _getAsyncData() {
    const {handleTabDZDetail, match: {params}} = this.props
    handleTabDZDetail({searchParams: {orderCode: params.id}})
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {history} = this.props
    history.length > 1 ? history.goBack() : window.close()
  }
  // 确认签收
  signFor = () => {
    const {modalData, signQty, handleSignFor} = this.props
    console.log(modalData.shippedOrderItemList)
    let obj = {}
    obj.header = {
      shippedOrderCode: modalData.orderCode,
      updateBy: modalData.shippedBatchList && modalData.shippedBatchList.createBy
    }
    obj.items = []
    modalData.shippedOrderItemList && modalData.shippedOrderItemList.map(o => {
      obj.items.push({productCode: o.productCode, qty: o.signQty, remark: o.remark})
    })
    console.log(obj)
    handleSignFor({data: obj}, (data) => {
      message.success(data.result)
      this._getAsyncData()
    })
  }
  render() {
    const {modalData, deliveryTableLoading, activeKey, update, isShowDzSplit, outWareStartSigning} = this.props
    return (
      <div className='fullOrderDel'>
        <span className='ant-modal-close-x close' onClick={this.handleCloseModal}></span>
        <Tabs activeKey={activeKey} onChange={(key) => {
          update({activeKey: key})
        }}>
          <TabPane tab={'出库单详情'} key="1">
            <div className='content'>
              <div className='header'>
                <Row type="flex" justify="start">
                  <Col span={6} className='delBox'>
                    <label>出库单号：</label>
                    <span>{modalData && modalData.orderCode || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>外部单号：</label>
                    <span>{modalData && modalData.externalOrderCode || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>出库地点：</label>
                    <span>{`${modalData && modalData.sendLocationCode || ''}-${modalData && modalData.sendLocationName || ''}`}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>货主：</label>
                    <span>{`${modalData && modalData.shipperCode || ''}-${modalData && modalData.shipperName || ''}`}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>业务类型：</label>
                    <span>{modalData && modalData.businessType || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>订单状态：</label>
                    <span>{modalData && modalData.statusDesc || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>出库单打印次数：</label>
                    <span>{modalData && modalData.printTimes || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>发货区域：</label>
                    <span>{modalData && modalData.pickingSortNumber}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>客户名称：</label>
                    <span>{`${modalData && modalData.customerCode || ''}-${modalData && modalData.customerName || ''}`}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>子客户名称：</label>
                    <span>{`${modalData && modalData.subCustomerCode || ''}-${modalData && modalData.subCustomerAddress || ''}`}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>联系人：</label>
                    <span>{modalData && modalData.receiveName || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>联系人电话：</label>
                    <span>{modalData && modalData.receivePhoneNumber || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>收货地址：</label>
                    <span>{modalData && modalData.receiveAddress || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>出库时间：</label>
                    <span>{modalData && modalData.sendTime || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>订单类型：</label>
                    <span>{modalData && modalData.orderType || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>签收日期：</label>
                    <span>{modalData.signDate || ''}</span>
                  </Col>
                  <Col span={6} className='delBox'>
                    <label>备注：</label>
                    <span>{modalData && modalData.remark || ''}</span>
                  </Col>
                </Row>
                <Row type="flex" justify="center" gutter={16} style={{marginTop: '28px'}}>
                  <Col className='delBox'>
                    <label>商品数量（已出/总量）：</label>
                    <span>{modalData.shippedProductCount} /{modalData.productCount}</span>
                  </Col>
                  <Col className='delBox'>
                    <label>商品种类（已出/总量）：</label>
                    <span>{modalData.shippedProductCategoryCount} /{modalData.productCategoryCount}</span>
                  </Col>
                  {
                    (this.tableActiveKey === '6' || this.tableActiveKey === '8') ?
                      <Col className='delBox'>
                        <label>商品金额：</label>
                        <span>{modalData.orderAmount}</span>
                      </Col> :
                      null
                  }

                </Row>
                <Row type="flex" justify="end">
                  <Col>
                    {outWareStartSigning && modalData.status == 6 && modalData.entruckingFlag == 0 && <Button type="primary" onClick={this.signFor}>确认签收</Button>}
                  </Col>
                </Row>
              </div>
              <div className='foot'>
                <div className='table-group'>
                  <Table
                    rowKey={(record, index) => index}
                    loading={deliveryTableLoading}
                    columns={modalData.shippedType == 'S01' && modalData.status == 6 || modalData.status == 8 || modalData.status == 7 ? this.columnsType : this.columns}
                    scroll={{x: 'max-content', y: 400}}
                    dataSource={modalData.shippedOrderItemList}
                    pagination={false}
                  />
                </div>
                {
                  modalData.shippedType == 'S01' && <div className='table-group'>
                    <Table
                      rowKey={(record, index) => index}
                      loading={deliveryTableLoading}
                      columns={this.columns1}
                      scroll={{x: 'max-content', y: 400}}
                      dataSource={modalData.groupItemResponseList}
                      pagination={false}
                    />
                  </div>
                }
              </div>
            </div>
          </TabPane>
          <TabPane tab={`出库明细（${modalData.shippedBatchCount}）`} key="2">
            {
              modalData.shippedBatchList && modalData.shippedBatchList.map((o, i) => <div className='table-group' key={o.batchCode}>
                <div className='header'>
                  <Row type="flex" justify="start">
                    <Col span={6} className='delBox'>
                      <label>出库批次：</label>
                      <span>{o.batchCode || ''}</span>
                    </Col>
                    <Col span={6} className='delBox'>
                      <label>创建时间：</label>
                      <span>{o.createTime || ''}</span>
                    </Col>
                    <Col span={6} className='delBox'>
                      <label>操作人：</label>
                      <span>{o.createBy || ''}</span>
                    </Col>
                    <Col span={6} className='delBox'>
                      <label>是否冲销：</label>
                      <span>{o.writeOffFlag ? '已冲销' : '未冲销'}</span>
                    </Col>
                  </Row>
                </div>
                <Table
                  rowKey={(record, index) => index}
                  loading={deliveryTableLoading}
                  columns={this.columns2}
                  scroll={{x: 'max-content', y: 400}}
                  dataSource={o.shippedBatchDetailList}
                  pagination={false}
                />
              </div>)
            }
          </TabPane>
        </Tabs>
        {
          isShowDzSplit && <DZsplitOrderModal />
        }
      </div>
    )
  }
}



