import React, {PureComponent} from 'react'
import {
  Modal,
  Table
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.outWarehouseListSell}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
      
    }
    this.columns = [{
      title: '序号',
      width: 100,
      dataIndex: 'num',
      key: 'num',
      render: (text, record, index) => index + 1
    }, {
      title: '商品编码',
      width: 100,
      dataIndex: 'productCode',
      key: 'productCode'
    }, {
      title: '商品名称',
      width: 100,
      dataIndex: 'productName',
      key: 'productName'
    }, {
      title: '基础单位',
      width: 100,
      dataIndex: 'unit',
      key: 'unit'
    }, {
      title: '下单数',
      width: 100,
      dataIndex: 'planQty',
      key: 'planQty'
    }, {
      title: '发货数',
      width: 100,
      dataIndex: 'shippedQty',
      key: 'shippedQty'
    }]
  }
  componentDidMount() {
    this._getAsyncData()
  }
  // 根据tabs的key来判断请求哪个接口
  _getAsyncData() {
    const {handleGetDzDetailRecord, dzOrderCode, dzSplitGroupCode} = this.props
    handleGetDzDetailRecord({searchParams: {orderCode: dzOrderCode, splitGroupCode: dzSplitGroupCode}})
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowDzSplit: false})
  }
  render() {
    const {dzSplitTableLoading, searchResultRecord} = this.props
    return (
        <Modal
          title="拆单详情"
          visible={true}
          onCancel={this.handleCloseModal}
          footer={null}
          mask={false}
          width={777}
        >
          <div className='table-group' style={{margin: 0}}>
            <Table
              rowKey={(record, index) => index}
              loading={dzSplitTableLoading}
              columns={this.columns}
              scroll={{x: 'max-content', y: 400}}
              dataSource={searchResultRecord}
              pagination={false}
            />
          </div>
        </Modal>
    )
  }
}



